const cur_Struct = {
    '$t': str => (NOT_I18n[str] || str)
};


Date.prototype.getMyFormat = function() {
    let MM = this.getMonth() + 1;
    let DD = this.getDate();
    let YY = this.getFullYear();
    let mm = this.getMinutes();
    let hh = this.getHours();

    return [
        (DD > 9 ? '' : '0') + DD + '.',
        (MM > 9 ? '' : '0') + MM + '.',
        YY + '_',
        (hh > 9 ? '' : '0') + hh + ':',
        (mm > 9 ? '' : '0') + mm
    ].join('');
};

